<template>
  <div id="recommend">
    <!-- 头部 -->
    <header>
      <div class="content">
        <div class="SmallImg">
          <img :src="topList.coverImgUrl" alt="" />
        </div>
        <div class="text">
          <div class="tag">精品歌单</div>
          <div class="title">{{topList.name}}</div>
          <h5 class="detailed">{{topList.description}}</h5>
        </div>
      </div>
      <div class="BigImg">
        <img :src="topList.coverImgUrl" alt="" />
      </div>
    </header>
    <!-- 内容 -->
    <main>
      <div class="label">
        <!-- prominent高亮属性名  点击时将对应的名字赋值给tag实现点击高亮 -->
        <div class="item" :class="{prominent:tag=='全部'}" @click="tag='全部'">全部</div>
        <div class="item" :class="{prominent:tag=='流行'}" @click="tag='流行'">流行</div>
        <div class="item" :class="{prominent:tag=='摇滚'}" @click="tag='摇滚'">摇滚</div>
        <div class="item" :class="{prominent:tag=='民谣'}" @click="tag='民谣'">民谣</div>
        <div class="item" :class="{prominent:tag=='电子'}" @click="tag='电子'">电子</div>
        <div class="item" :class="{prominent:tag=='另类/独立'}" @click="tag='另类/独立'">另类/独立</div>
        <div class="item" :class="{prominent:tag=='轻音乐'}" @click="tag='轻音乐'">轻音乐</div>
        <div class="item" :class="{prominent:tag=='综艺'}" @click="tag='综艺'">综艺</div>
        <div class="item" :class="{prominent:tag=='影视原声'}" @click="tag='影视原声'">影视原声</div>
        <div class="item" :class="{prominent:tag=='ACG'}" @click="tag='ACG'">ACG</div>
      </div>
      <div class="content">
        <div class="detailed" v-for="(item, index) in playList" :key="index">
          <div class="top">
            <div class="text">
              <div class="SmallPlay">
                <span class="iconfont icon-24gl-play"></span>
                <span>{{item.playCount}}</span>
              </div>
            </div>
            <div class="Img">
              <img v-lazy="item.coverImgUrl" alt="" />
            </div>
            <div class="play">
              <img src="../assets/images/play.png" alt="" />
            </div>
          </div>
          <div class="title">{{item.name}}</div>
        </div>
      </div>
    </main>
    <!-- 页尾 -->
    <footer>
      <div class="paging">
        <!-- total 总条数
        current-page 当前也
        page-size 每页多少条数据
        current-change 页码改变事件
         -->
        <el-pagination
        @current-change="fenyeqi"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        :page-size="40">
        </el-pagination>
      <!-- offset: 偏移数量 , 用于分页 , 如 :( 评论页数 -1)*50, 其中 50 为 limit 的值 -->
      </div>
    </footer>
    <!-- 返回顶部 -->
    <div class="backT">
      <back-top></back-top>
    </div>
      <!-- 占位 -->
    <div class="zhanwei"></div>
  </div>
</template>

<script>
import backTop from '../components/backTop.vue'
import axios from 'axios'
export default {
  components: {
    backTop
  },
  data () {
    return {
      // 分页器的数据：
      // 总条数
      total: 0,
      // 页码
      page: 1,
      // 顶部推荐歌单 应为获取是一个对象
      topList: {},
      // 歌单列表
      playList: [],
      // 保存当前选中的分类
      tag: '全部'
    }
  },
  // 侦听器 当对应的tag发生改变时，改变目录
  watch: {
    tag () {
      // console.log(this.tag)
      // 顶部推荐歌单
      this.topData()
      // 歌单列表
      this.listData()
      // 修改页码唯1
      this.page = 1
    }
  },
  created () {
    // 顶部推荐歌单
    this.topData()
    // 歌单列表
    this.listData()
  },
  methods: {
    // 顶部推荐歌单
    async topData () {
      const { data } = await axios({
        url: 'https://autumnfish.cn/top/playlist/highquality',
        method: 'get',
        params: {
          limit: 1,
          cat: this.tag
        }
      })
      // console.log(data)
      if (data.code === 200) {
        this.topList = data.playlists[0]
      }
    },
    // 歌单列表
    async listData () {
      const { data } = await axios({
        url: 'https://autumnfish.cn/top/playlist',
        method: 'get',
        params: {
          limit: 40,
          cat: this.tag,
          offset: (this.page - 1) * 40
          // offset: 偏移数量 , 用于分页 , 如 :( 页数 -1)*8, 其中 8 为 limit 的值
        }
      })
      // console.log(data)
      if (data.code === 200) {
        this.playList = data.playlists
        // 处理播放次数
        for (let i = 0; i < this.playList.length; i++) {
          if (this.playList[i].playCount > 100000) {
            this.playList[i].playCount = parseInt(this.playList[i].playCount / 10000) + '万'
          }
        }
        // 保存总条数
        this.total = data.total
      }
    },
    // 页码改变事件
    fenyeqi (val) {
      // console.log('当前第：' + val + '页')
      // val页码数
      // 保存页码数
      this.page = val
      // 重新获取数据
      this.listData()
      this.top()
    },
    // 回到顶部
    top () {
      if (document.body.scrollTop) {
        document.body.scrollTop = 0
      } else {
        document.documentElement.scrollTop = 0
      }
    }
  }
}
</script>

<style src='../assets/css/IN.recommend.less' lang="less" scoped></style>
<style scoped>
#recommend .backT{
  position: fixed;
  top: 250px;
  right: 50px;
}
main .detailed .text .SmallPlay .icon-24gl-play{
    margin-top: 3px;
}
</style>
